body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    background-color: aliceblue;
    box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
    height: 4em;
    }

.nav-container{
    width: 90%;
    
    display: flex; 
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    margin: auto;
}

.nav-menu{
    width:80%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    grid-gap: 3em;
    gap: 3em;
    align-items: baseline;
    margin: 0;
}

.app-title{
    font-size: 2.3em;
    font-weight: bolder;
    text-decoration: none;
    color:rgba(0, 0, 0, 0.8);
    font-family: 'Brush Script MT', cursive;
}

.nav-elements{
    text-decoration: none;
    color:rgba(0, 0, 0, 0.8);
}

.profile-menu{
    text-decoration: none;
    color:rgba(0, 0, 0, 0.8);
    background-color: aliceblue;
    border-color: none;
    box-shadow: none;
    border-width: 0px;
}

.profile-menu:hover{
    text-decoration: none;
    color:rgba(0, 0, 0, 0.8);
    background-color: aliceblue;
    color: darkcyan;
    border-color: none;
    box-shadow: none;
    border-width: 0px;
}

* {
    font-family: Arial, Helvetica, sans-serif;
}

.App {
  background-color: white;
}

.Auth-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 80vh;
}

.Auth-form {
  width: 420px;
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 10px;
  padding-top: 30px;
  padding-bottom: 20px;
  border-radius: 8px;
  background-color: white;
}

.Auth-form-content {
  padding-left: 12%;
  padding-right: 12%;
}

.Auth-form-title {
  text-align: center;
  margin-bottom: 1em;
  font-size: 24px;
  color: rgb(34, 34, 34);
  font-weight: 800;
}

.auth-btn{
  background-color: darkslateblue; 
  border: none;
  border-radius: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
  text-align: center;
  display: inline-block;
}

.auth-btn:hover, .auth-btn[hover]{
  background-color: darkblue;
  border: none;
  border-radius: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
  text-align: center;
  display: inline-block;
}

.auth-btn:disabled,
.auth-btn[disabled]{
  background-color: aliceblue;
  border: none;
  border-radius: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  text-align: center;
  color: #000000;
  display: inline-block;

}

.post-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.post-form{

  width: 100%;
  padding: 5px;
  justify-content: center;
  border-radius: 8px;
  background-color: white;

}

.post-form-title{
  text-align: left;
  font-size: 1.5em;
  border: 0px;
}

.card-body{
  padding: 0.8em;
}

.card-body a {
  color: #000000;
  text-decoration: none;
}

.card-body a:hover {
  color:darkcyan;
  text-decoration: underline;
}

.card-subtitle{
  font-size: 0.8em;
}

.card-title{
  font-size: 1.5em;
}

.card-title-comment{
  margin-top: 0.3em;
  font-size: 1.5em;
}

.card-text-comment{
  font-size: 1.1em;
  margin-top: 1em;
}

.card-container{
  display:flex;
  margin-bottom: 1em;
  border: #000000;
  flex-direction: row;
  justify-content: center;
  
}

.card{
  width: 90%;
  border-color: aliceblue;
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 3px;
  border-width: 1.5px;
  border-radius: 10px;
}

.card-comment{
  border-color: aliceblue;
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 3px;
  border-width: 1.5px;
  border-radius: 10px;
  margin-bottom: 1em;
}

.card-body{
  margin-left: 1em;
  display:flex;
  justify-content: left;
  flex-direction: column;
}

.card-dropdown{
  height: 30px;
}

.card-top-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.card-text{
  padding: 0px;
  margin: 0px;
  font-size: 1.2em;
}

.container-row{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 1em;

}

.post-input{
  width: 100%;
  border: 1px solid darkcyan;
  display: block;
  padding: 9px 4px 9px 30px;
  border-radius: 30px;
  height: 1.7em;
  margin-top: 5px;
  background: transparent url(data:image/png;base64,) no-repeat 8px;
}

.form-input {
  border: none;
  margin-left: 3em;
  padding: 0;
}

.form-input :focus{
  outline: none;
  border-width: 2px;
  border-color: darkslateblue;
}

.mainpage{
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: auto;
  padding-top: 40px;
}

/* label {
  font-size: 14px;
  font-weight: 600;
  color: rgb(34, 34, 34);
} */

.btn{
  background-color: white;
  border: white;
  color: black;
}

.btn:focus,.btn:hover, .btn[hover],
.btn[focus]{
  background-color: aliceblue;
  border: white;
  color: black;
}

.form-btn{
  background-color: aliceblue;
  border: none;
  color: black;
  height: 2.5em;
  width: 5em;
  box-shadow: rgb(0 0 0 / 16%) 1px 1px 3px; 
  border-radius: 10px;
  text-align: center;
  font-size: 1em;
  float: right;
  margin-top: 0.8em;
}

.form-btn:disabled,
.form-btn[disabled]{
  background-color: white;
  border-color: aliceblue;
  color: rgb(0 0 0 / 20%);
  height: 2.5em;
  width: 5em;
  border-radius: 10px;
  text-align: center;
  font-size: 1em;

}
